* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  height: 100vh;
  overflow: hidden;
  background-color:#195e83;
}

.text {
  font-size: 3rem;
  font-weight: bold;
  color: rgb(255, 255, 255);
  letter-spacing: 1px;
}